import React, { Component, Fragment } from "react";
import styled from "styled-components";

export default class extends Component {
  render() {
    const style = {
      backgroundImage: "url(" + this.props.image + ")",
      backgroundSize: "cover",
      backgroundPosition: "center"
    };
    return (
      <Fragment>
        <a href={this.props.url} title={this.props.bannerTitle}>
          <section className="banner thee" followButton={true}>
            <img src={this.props.authorImage} alt={this.props.bannerTitle} />
              <span className="bannerDesc">
                <h3 >{this.props.bannerTitle}</h3>
                <h6>{this.props.category}</h6>
              </span>
          </section>
        </a>
      </Fragment>
    );
  }
}



