import React, { Component, Fragment } from "react";

export default class extends Component {
  render() {
    return (
      <Fragment>
        <section className="header">
          <h1 className="siteName">{this.props.headerTitle}</h1>
          <h1 className="navRight"> &#9776;</h1>
        </section>
      </Fragment>
    );
  }
}



